import { useEffect, useRef, useState } from "react";
import axios from "axios";

const RegisteredCentersLayer = () => {
  const [centres, setCentres] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [selectedCentre, setSelectedCentre] = useState(null);
  const [showViewModal, setShowViewModal] = useState(null);

  const modalRef = useRef(null);
  const viewModalRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showModal
      ) {
        setShowModal(false);
      }
      if (
        viewModalRef.current &&
        !viewModalRef.current.contains(event.target) &&
        showViewModal
      ) {
        setShowViewModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // ðŸ”‘ Scroll lock logic
    if (showViewModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto";
    };
  }, [showViewModal]);

  const fetchCentres = async () => {
    setLoading(true);
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/completedProfile`,
      );
      setCentres(res.data || []);
    } catch (err) {
      console.error("Error fetching centres:", err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCentres();
  }, []);

  const handleAction = async (id, action) => {
    const confirmText =
      action === "Approved"
        ? "Are you sure you want to approve this?"
        : "Are you sure you want to reject this?";

    if (window.confirm(confirmText)) {
      try {
        await axios.post(
          `${process.env.REACT_APP_BACKEND_URL}/api/centres/status-update`,
          {
            id,
            status: action,
          },
        );

        alert(`Successfully ${action}`);

        if (action === "Approved") {
          // Remove the approved record from current list
          setCentres((prev) => prev.filter((c) => c._id !== id));
        } else {
          // If rejected, update that specific centre's status to "Rejected"
          setCentres((prev) =>
            prev.map((c) => (c._id === id ? { ...c, status: "Rejected" } : c)),
          );
        }
      } catch (err) {
        console.error(err);
        alert("Action failed. Please try again.");
      }
    }
  };

  const [currentPage, setCurrentPage] = useState(1);
  const centresPerPage = 10;

  const indexOfLastCentre = currentPage * centresPerPage;
  const indexOfFirstCentre = indexOfLastCentre - centresPerPage;
  const currentCentres = centres.slice(indexOfFirstCentre, indexOfLastCentre);
  const totalCentresPages = Math.ceil(centres.length / centresPerPage);

  const handlePageChangeWithScroll = (page) => {
    handlePageChange(page); // your existing page change logic
    window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
  };

  const renderPagination = () => {
    const pages = [];
    const maxVisible = 3;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalCentresPages) {
      endPage = totalCentresPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

    //first button
    pages.push(
      <li
        key="first"
        className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
        >
          First
        </button>
      </li>,
    );

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)
          }
        >
          Prev
        </button>
      </li>,
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button
            className="page-link"
            onClick={() => handlePageChangeWithScroll(i)}
          >
            {i}
          </button>
        </li>,
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${
          currentPage === totalCentresPages ? "disabled" : ""
        }`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalCentresPages && handlePageChange(currentPage + 1)
          }
        >
          Next
        </button>
      </li>,
    );
    pages.push(
      <li
        key="last"
        className={`page-item ${currentPage === totalCentresPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalCentresPages)}
        >
          Last
        </button>
      </li>,
    );

    return pages;
  };

  const handlePageChange = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <div className="dashboard-body__content">
      <div className="card common-card border border-gray-five">
        <div className="card-body">
          <h4>Under Approval Centres</h4>
          {loading ? (
            <p>Loading centres...</p>
          ) : centres.length > 0 ? (
            <div className="table-responsive" style={{ marginTop: "-20px" }}>
              <table className="table text-body">
                <thead>
                  <tr>
                    <th>SN.</th>
                    <th>Centre Name</th>
                    <th>Centre Type</th>
                    <th>Email</th>
                    <th>Phone</th>
                    <th>Address</th>
                    <th className="text-center">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {currentCentres.map((centre, i) => (
                    <tr key={centre._id}>
                      <td>{indexOfFirstCentre + i + 1}</td>
                      <td>{centre.centreName}</td>
                      <td>{centre.centreType}</td>
                      <td>{centre.email}</td>
                      <td>{centre.phoneNumber}</td>
                      <td>{centre.address}</td>

                      <td>
                        <div className="d-flex gap-2 mt-2">
                          {/* View button - Always visible */}
                          <button
                            className="btn btn-sm btn-info"
                            onClick={() => {
                              setSelectedCentre(centre);
                              setShowViewModal("details");
                            }}
                            style={{ backgroundColor: "blue" }}
                          >
                            View
                          </button>

                          {/* Pending: Show Approve and Reject */}
                          {centre.status === "Pending" && (
                            <>
                              <button
                                className="btn btn-sm btn-success"
                                onClick={() =>
                                  handleAction(centre._id, "Approved")
                                }
                                style={{ backgroundColor: "green" }}
                              >
                                Approve
                              </button>
                              <button
                                className="btn btn-sm btn-danger"
                                onClick={() =>
                                  handleAction(centre._id, "Rejected")
                                }
                                style={{ backgroundColor: "red" }}
                              >
                                Reject
                              </button>
                            </>
                          )}

                          {/* Rejected: Show disabled Rejected button */}
                          {centre.status === "Rejected" && (
                            <button
                              className="btn btn-sm btn-danger"
                              style={{ backgroundColor: "red" }}
                            >
                              Rejected
                            </button>
                          )}
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <p>No registered centres found.</p>
          )}

          {currentCentres.length > 0 && totalCentresPages > 1 && (
            <nav aria-label="Service pagination" className="mt-3">
              <ul className="pagination justify-content-center">
                {renderPagination()}
              </ul>
            </nav>
          )}
        </div>

        {/* Modal for viewing documents */}
        {showModal && selectedCentre && (
          <div
            className="modal d-block"
            tabIndex="-1"
            role="dialog"
            style={{ backgroundColor: "rgba(0,0,0,0.5)" }}
          >
            <div className="modal-dialog modal-dialog-centered" role="document">
              <div
                className="modal-content"
                style={{ width: "550px" }}
                ref={modalRef}
              >
                <div className="modal-header">
                  <h5 className="modal-title">View Documents</h5>
                  <button
                    type="button"
                    className="btn-close"
                    onClick={() => setShowModal(false)}
                  ></button>
                </div>
                <div className="modal-body">
                  {selectedCentre.documents?.length > 0 ? (
                    <table className="table">
                      <thead>
                        <tr>
                          <th>SI.No</th>
                          <th>Doc Type</th>
                          <th>Action</th>
                        </tr>
                      </thead>
                      <tbody style={{ color: "black" }}>
                        {selectedCentre.documents.map((doc, index) => (
                          <tr key={index}>
                            <td>{index + 1}</td>
                            <td>{doc.docType}</td>
                            <td>
                              <a
                                className="btn btn-sm btn-primary"
                                href={doc.docFile}
                                target="_blank"
                                rel="noopener noreferrer"
                              >
                                View
                              </a>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  ) : (
                    <p>No documents found.</p>
                  )}
                </div>
              </div>
            </div>
          </div>
        )}

        {showViewModal && selectedCentre && (
          <div
            style={{
              position: "fixed",
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: "rgba(0,0,0,0.5)",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              zIndex: 1000,
            }}
          >
            <div
              ref={viewModalRef}
              style={{
                backgroundColor: "#fff",
                borderRadius: "10px",
                maxWidth: "800px",
                width: "95%",
                maxHeight: "95vh", // modal still limited to viewport height

                boxShadow: "0 0 10px rgba(0,0,0,0.3)",
                position: "relative",
                overflowY: "scroll",
              }}
              className="no-scrollbar"
            >
              <div
                style={{
                  backgroundColor: "#3a3a3a",
                  color: "#fff",
                  padding: "15px 20px",
                  fontSize: "18px",
                  fontWeight: "bold",
                  position: "sticky",
                  top: "0",
                  zIndex: 10,
                }}
              >
                Centre Details
                <button
                  onClick={() => setShowViewModal(false)}
                  style={{
                    position: "absolute",
                    top: "10px",
                    right: "10px",
                    background: "transparent",
                    border: "none",
                    fontSize: "22px",
                    color: "#fff",
                    cursor: "pointer",
                  }}
                >
                  &times;
                </button>
              </div>

              <div
                style={{
                  display: "flex",
                  flexWrap: "wrap",
                  padding: "20px",
                  gap: "20px",
                  color: "#333",
                }}
              >
                {/* Left Column */}
                <div style={{ flex: 1, minWidth: "250px" }}>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Centre Name</strong>
                    <p>{selectedCentre.centreName}</p>
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Centre Type</strong>
                    <p>{selectedCentre.centreType}</p>
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Email</strong>
                    <p>{selectedCentre.email}</p>
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Phone</strong>
                    <p>{selectedCentre.phoneNumber}</p>
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Landline</strong>
                    <p>
                      {selectedCentre.landlineNumber ||
                        "Landline not available"}
                    </p>
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Address</strong>
                    <p>{selectedCentre.address}</p>
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>ZIP Code</strong>
                    <p>{selectedCentre.zipCode}</p>
                  </div>

                  <div style={{ marginBottom: "15px" }}>
                    <strong>City</strong>
                    <p>{selectedCentre.city}</p>
                  </div>

                  <div style={{ marginBottom: "15px" }}>
                    <strong>State</strong>
                    <p>{selectedCentre.state}</p>
                  </div>
                </div>

                {/* Right Column */}
                <div style={{ flex: 1, minWidth: "250px" }}>
                  <div style={{ marginBottom: "15px" }}>
                    <strong style={{ color: "black" }}>Facilities</strong>
                    <ul style={{ marginTop: "5px" }}>
                      {selectedCentre.availableFacilities?.length ? (
                        selectedCentre.availableFacilities.map(
                          (facility, index) => <li key={index}>{facility}</li>,
                        )
                      ) : (
                        <li>None</li>
                      )}
                    </ul>
                  </div>

                  <div style={{ marginBottom: "15px" }}>
                    <strong>DigiPin Location</strong>
                    {selectedCentre.digiPinLocation ? (
                      <p>{selectedCentre.digiPinLocation}</p>
                    ) : (
                      <p>DigiPin not available</p>
                    )}
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Authorized Person</strong>
                    <p>
                      Name: {selectedCentre.authorizedPerson?.name || "N/A"}{" "}
                      <br />
                      Designation:{" "}
                      {selectedCentre.authorizedPerson?.designation || "N/A"}
                    </p>
                    {selectedCentre.authorizedPerson?.authDocument && (
                      <a
                        href={selectedCentre.authorizedPerson.authDocument}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{
                          display: "inline-block",
                          marginTop: "5px",
                          fontSize: "14px",
                          color: "#007bff",
                        }}
                      >
                        View Document
                      </a>
                    )}
                  </div>

                  {selectedCentre.documents?.length > 0 && (
                    <div style={{ marginBottom: "15px" }}>
                      <strong style={{ color: "black" }}>Documents</strong>
                      <ul style={{ marginTop: "5px" }}>
                        {selectedCentre.documents.map((doc, index) => (
                          <li key={index}>
                            {doc.docType} -{" "}
                            <a
                              href={doc.docFile}
                              target="_blank"
                              rel="noopener noreferrer"
                              style={{ color: "#007bff", fontSize: "14px" }}
                            >
                              View Document
                            </a>
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default RegisteredCentersLayer;
